"use client";
import React, { useState } from 'react';
import { DollarSign, Briefcase, Megaphone, Check, Shield, Globe } from 'lucide-react';
import '@/styles/pages/GetStarted.css';

interface PricingTier {
    id: string;
    name: string;
    description: string;
    monthlyPrice: number;
    oneTimePrice: number;
    icon: React.ReactNode;
}

const INSURANCE_ADDON: PricingTier = {
    id: 'insurance',
    name: 'Business Legal Insurance',
    description: 'Protect your venture with professional legal support and insurance protocols.',
    monthlyPrice: 99.00,
    oneTimePrice: 0,
    icon: <Shield size={32} />
};

const FINANCE_ADDON: PricingTier = {
    id: 'finance',
    name: 'Finance Division',
    description: 'Advanced fintech tools, yield vaults, and risk assessment protocols.',
    monthlyPrice: 149.00,
    oneTimePrice: 0,
    icon: <DollarSign size={32} />
};

const SALES_ADDON: PricingTier = {
    id: 'sales',
    name: 'Sales Division',
    description: 'Access to the Rank Simulator, Genealogy Visualizer, and Earning Logic.',
    monthlyPrice: 75.00,
    oneTimePrice: 149.00,
    icon: <Megaphone size={32} />
};

export default function GetStartedPage() {
    const [addons, setAddons] = useState<string[]>([]);

    const toggleAddon = (id: string) => {
        if (addons.includes(id)) {
            setAddons(addons.filter(a => a !== id));
        } else {
            setAddons([...addons, id]);
        }
    };

    // Calculations
    const coreMonthly = 0.00;

    let totalMonthly = coreMonthly;
    let totaltoday = coreMonthly;

    if (addons.includes('insurance')) {
        totalMonthly += INSURANCE_ADDON.monthlyPrice;
        totaltoday += INSURANCE_ADDON.monthlyPrice;
    }

    if (addons.includes('finance')) {
        totalMonthly += FINANCE_ADDON.monthlyPrice;
        totaltoday += FINANCE_ADDON.monthlyPrice;
    }

    if (addons.includes('sales')) {
        totalMonthly += SALES_ADDON.monthlyPrice;
        totaltoday += SALES_ADDON.oneTimePrice + SALES_ADDON.monthlyPrice;
    }

    return (
        <div className="get-started-page animate-fade-in">
            <div className="gs-container">
                {/* Left Column: Selection */}
                <div className="selection-area">
                    <div className="gs-header">
                        <h1>Initialize Your Access</h1>
                        <p>Customize your Verstige Operating System configuration. Start for free and scale as you grow.</p>
                    </div>

                    {/* CORE MEMBERSHIP (Always Included) */}
                    <div className="core-plan-card">
                        <div className="core-info">
                            <h3>Initial Access <span className="badge-included">FREE</span></h3>
                            <p className="text-secondary text-sm mb-2">Immediate Entry to the Business Division</p>
                            <ul className="text-sm text-secondary space-y-1">
                                <li className="flex items-center gap-2"><Check size={14} className="text-green-500" /> Business Setup Blueprint</li>
                                <li className="flex items-center gap-2"><Check size={14} className="text-green-500" /> Educational Resource Mesh</li>
                                <li className="flex items-center gap-2"><Check size={14} className="text-green-500" /> Central Dash Preview</li>
                            </ul>
                        </div>
                        <div className="core-price">
                            <span className="price-large">$0.00</span>
                            <span className="price-period">/ month</span>
                        </div>
                    </div>

                    <h3 className="text-lg font-bold mb-4 uppercase text-secondary tracking-wider">Strategic Expansions</h3>

                    <div className="addon-grid">
                        {/* INSURANCE ADDON (Recommended First) */}
                        <div
                            className={`addon-card ${addons.includes('insurance') ? 'selected' : ''}`}
                            onClick={() => toggleAddon('insurance')}
                        >
                            <div className="selection-indicator"></div>
                            <div className="addon-icon"><Shield size={32} /></div>
                            <div className="addon-content">
                                <div className="flex items-center gap-2">
                                    <h4>{INSURANCE_ADDON.name}</h4>
                                    <span className="text-[10px] bg-blue-500/20 text-blue-400 px-2 py-0.5 rounded uppercase font-bold">Recommended</span>
                                </div>
                                <p className="addon-desc">{INSURANCE_ADDON.description}</p>
                                <div className="addon-pricing">
                                    <span className="price-tag highlight">+$99.00 / mo</span>
                                </div>
                            </div>
                        </div>

                        {/* FINANCE ADDON */}
                        <div
                            className={`addon-card ${addons.includes('finance') ? 'selected' : ''}`}
                            onClick={() => toggleAddon('finance')}
                        >
                            <div className="selection-indicator"></div>
                            <div className="addon-icon">{FINANCE_ADDON.icon}</div>
                            <div className="addon-content">
                                <h4>{FINANCE_ADDON.name}</h4>
                                <p className="addon-desc">{FINANCE_ADDON.description}</p>
                                <div className="addon-pricing">
                                    <span className="price-tag highlight">+$149.00 / mo</span>
                                </div>
                            </div>
                        </div>

                        {/* SALES ADDON */}
                        <div
                            className={`addon-card ${addons.includes('sales') ? 'selected' : ''}`}
                            onClick={() => toggleAddon('sales')}
                        >
                            <div className="selection-indicator"></div>
                            <div className="addon-icon">{SALES_ADDON.icon}</div>
                            <div className="addon-content">
                                <h4>{SALES_ADDON.name}</h4>
                                <p className="addon-desc">{SALES_ADDON.description}</p>
                                <div className="addon-pricing flex-col gap-0">
                                    <span className="price-tag highlight">+$75.00 / mo</span>
                                    <span className="text-xs text-secondary">+$149.00 One-time Activation</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Right Column: Summary */}
                <div className="order-summary">
                    <h3>Configuration Summary</h3>

                    {/* Core Line Item */}
                    <div className="summary-row">
                        <span>Free Initial Access</span>
                        <span className="text-green">FREE</span>
                    </div>

                    {/* Insurance Line Item */}
                    {addons.includes('insurance') && (
                        <div className="summary-row animate-fade-in">
                            <span>Business Insurance</span>
                            <span>$99.00</span>
                        </div>
                    )}

                    {/* Finance Line Item */}
                    {addons.includes('finance') && (
                        <div className="summary-row animate-fade-in">
                            <span>Finance Add-on</span>
                            <span>$149.00</span>
                        </div>
                    )}

                    {/* Sales Line Item */}
                    {addons.includes('sales') && (
                        <>
                            <div className="summary-row animate-fade-in">
                                <span>Sales Add-on</span>
                                <span>$75.00</span>
                            </div>
                            <div className="summary-row sub-item animate-fade-in">
                                <span>Sales Activation Fee</span>
                                <span>$149.00</span>
                            </div>
                        </>
                    )}

                    <div className="summary-total">
                        <div className="total-row">
                            <span className="text-secondary">Due Today</span>
                            <span className="grand">${totaltoday.toFixed(2)}</span>
                        </div>
                        <div className="total-row">
                            <span className="text-secondary text-sm">Monthly Recurring</span>
                            <span className="font-mono text-white">${totalMonthly.toFixed(2)}</span>
                        </div>
                    </div>

                    <button className="btn-checkout">
                        Complete Configuration
                    </button>
                    <p className="text-xs text-center mt-4 text-secondary">
                        Secure 256-bit automated encryption. No credit card required for free access.
                    </p>
                </div>
            </div>
        </div>
    );
}
